@import "./_variables.scss";
@import "./_themes.scss";
@import "./_mixin.scss";
//样式重写，这里其实就跟ant design vue的base.less基本一致，所以这里重写也覆盖预览页面，这是基础样式，所有组件都以此样式为基础
body{
    margin: 0;
    padding: 0;
    color: rgba(0,0,0,.65);
    font-size: 14px;
    font-variant: tabular-nums;
    line-height: 1.4;
    background-color: #fff;
    font-feature-settings: "tnum";
}
* {
    font-family: pingfang SC, helvetica neue, arial, hiragino sans gb, microsoft yahei ui, microsoft yahei, simsun, sans-serif;
}
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
*,
*::before,
*::after {
  box-sizing: border-box; // 1
}
@-ms-viewport {
    width: device-width;
}

// IE10+ doesn't honor `<meta name="viewport">` in some cases.
@-ms-viewport {
    width: device-width;
}

// Shim for "new" HTML5 structural elements to display correctly (IE10, older browsers)
article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
    display: block;
}
img{
    vertical-align: middle;
    border-style: none;
}
.idm_module_user_defined{
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 999999;
    border-width: 3px;
    border-style: solid;
    border-color: #1890ff;
    cursor: move;
    >div{
        cursor: pointer;
        position: absolute;
        right: 0px;
        top: 0px;
        width: 28px;
        height: 18px;
        text-align: center;
        border-bottom-left-radius: 9px;
        background-color: rgba(0,0,0,.4);
        transition: all linear 0.2s;
        svg{
            fill: currentColor;
            color: white;
        }
        &:hover{
            opacity: 0.9;
        }
    }
}
.idm_module{
    position: relative;
    max-width: 100%;
    //用户个性化定制的组件外框样式
    &>.idm_module_user_defined{
        display: none;
    }
    &:hover{
        &>.idm_module_user_defined{
            display: block;
        }
    }
}
//移动的盒子样式
.idm_module_user_defined_movebox{
    .idm_module_user_defined{
        display: block;
    }
}
//占位盒子样式
.idm_module_user_defined_placeholderbox{
    border: 1px dashed #c9c9c9;
    background-color: #F6F6F6;
}
//容器悬浮占位样式
.idm_module_user_defined_placeholderstyle::before{
    content: " ";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 999999;
    background-color: rgba(0,0,0,.35);
    border: 1px dashed #666666;
    transition: all linear 0.2s;
}
.idm_page_user_defined_topbar{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 9999999;
    height: 60px;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0,0,0,.4);
    display: flex;
    justify-content: space-between;
    align-items: center;
    .idm_page_user_defined_tip{
        color: #cccccc;
        font-size: 14px;
    }
    .idm_page_user_defined_logo{
        display: flex;
        align-items: center;
        padding-left: 10px;
        font-size: 18px;
        color: #666666;
        svg{
            fill: currentColor;
            &.fold-svg{
                margin-left:50px;
                color:#999999;
                cursor: pointer;
                opacity: 0.8;
                transition: all linear 0.2s;
                &:hover{
                    opacity: 1;
                }
            }
        }
    }
}
.idm_page_user_defined_topbar-enter-active, .idm_page_user_defined_topbar-leave-active,
.idm_page_user_defined_leftbar-enter-active, .idm_page_user_defined_leftbar-leave-active {
    transition: all .2s;
}
.idm_page_user_defined_topbar-enter, .idm_page_user_defined_topbar-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateY(-60px);
}
.idm_page_user_defined_leftbar{
    position: fixed;
    left: 0;
    top: 60px;
    bottom: 0;
    width: 190px;
    z-index: 9999998;
    background-color: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0,0,0,.4);
    padding-top: 10px;
    overflow: auto;
    user-select: none;
    .idm_page_user_defined_allmodule_item{
        height: 40px;
        line-height: 40px;
        padding: 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 14px;
        color: #333333;
        &:hover{
            background-color: rgba(0,0,0,.05);
        }
        &.hover-show-remove:hover{
            .user-defined-remove-svg{
                display: block;
            }
        }
        span{
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
        &_op{
            display: flex;
            align-items: center;
            svg{
                fill: currentColor;
                margin:auto 2px;
                cursor: pointer;
                &.user-defined-selected-svg{
                    color: #cccccc;
                    cursor:default;
                }
                &.user-defined-remove-svg{
                    color: #E21A1A;
                    display: none;
                }
                &.user-defined-add-svg{
                    color: #1890ff;
                }
            }
        }
    }
}
.idm_page_user_defined_leftbar-enter, .idm_page_user_defined_leftbar-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateX(-190px);
}
.idm_page_user_defined_modal_alert{
    box-sizing: border-box;
    margin: 0;
    color: rgba(0,0,0,.65);
    font-size: 14px;
    font-variant: tabular-nums;
    line-height: 1.5;
    list-style: none;
    font-feature-settings: "tnum";
    position: relative;
    word-wrap: break-word;
    border-radius: 4px;
    background-color: #fffbe6;
    border: 1px solid #ffe58f;
    padding: 8px 15px;
    span{
        font-weight: bold;
    }
}
.idm_page_user_defined_modal_modulelist{
    margin-top: 15px;
}
.idm_page_user_defined_modal_moduleitem{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    line-height: 40px;
    padding: 0px 15px;
    border-radius: 4px;
    transition: all linear 0.2s;
    &>div:first-child{
        font-size: 16px;
        width: 80%;
        display: flex;
        align-items: center;
        span{
            font-size: 12px;
            color:#cccccc ;
            margin-left: 15px;
        }
        label{
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            width: 60%;
        }
    }
    &:hover,&.active{
        background-color: #F2F2F2;
    }
    &>div:last-child{
        div{
            display: flex;
            align-items: center;
        }
        svg{
            fill: currentColor;
            margin-right: 5px;
        }
        .user-defined-modal-selected{
            color: #AAAAAA;
            cursor: default;
        }
        .user-defined-modal-changeseats,.user-defined-modal-replace{
            cursor: pointer;
            opacity: 1;
            color: #1890ff;
            &:hover{
                opacity: 0.8;
            }
        }
    }
}