@import "@/style/common/common.scss";









































































































































































































































































































































































































































































































































































































































































































































































































html,
body,
body > div.idm-root-element,
body > div .idm_render_engine_container,
body > div .idm_render_engine_router_container,
body > div .idm_render_engine_page_container,
body > div .idm_page_root {
  min-height: 100%;
  position: relative;
}
body > div.idm-root-element,
body > div .idm_render_engine_container,
body > div .idm_render_engine_router_container,
body > div .idm_render_engine_page_container {
  height: 100%;
}
.idm_page_root{
  overflow:auto
}
/**
控制中心抽屉
*/
.idm_module_setting_drawer{
  .idm-ant-drawer-wrapper-body{
    display: flex;
    flex-direction: column;
    .idm-ant-drawer-body{
      flex-grow: 2;
      padding: 0;
      .idm_drawer_iframe_basestyle{
        border: none;
        width: 100%;
        height: 100%;
      }
    }
  }
}

/*
模态窗口容器
*/
.idm_dialog_wrap_preview{
    position: fixed;
    top: 0px;
    right: 0px;
    left: 0px;
    bottom: 0px;
    z-index: 59;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    // animation:idm_dialog_wrap_shade_animation 0.3s;
    /*Safari and Chrome*/
    // -webkit-animation:idm_dialog_wrap_shade_animation 0.3s;
    .idm_dialog_shade_layer{
      // background: transparent;
      background-color: rgba(0,0,0,.4);
      animation:idm_dialog_wrap_shade_animation 0.3s;
      /*Safari and Chrome*/
      -webkit-animation:idm_dialog_wrap_shade_animation 0.3s;
      // z-index: 0;
      &[hideing='hideing']{
        animation:idm_dialog_wrap_shade_animation_hide 0.52s;
        /*Safari and Chrome*/
        -webkit-animation:idm_dialog_wrap_shade_animation_hide 0.52s;
      }
    }
    &>div{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
    }
    &>div.idm_module{
      display: none;
      pointer-events:none;
      &>div{
        pointer-events:auto
      }
      &.topLeft{
        display: flex;
        align-items:flex-start;
        justify-content:flex-start;
      }
      &.topCenter{
        display: flex;
        align-items:flex-start;
        justify-content:center;
      }
      &.topRight{
        display: flex;
        align-items:flex-start;
        justify-content:flex-end;
      }
      &.centerLeft{
        display: flex;
        align-items:center;
        justify-content:flex-start;
      }
      &.center{
        display: flex;
        align-items:center;
        justify-content:center;
      }
      &.centerRight{
        display: flex;
        align-items:center;
        justify-content:flex-end;
      }
      &.bottomLeft{
        display: flex;
        align-items:flex-end;
        justify-content:flex-start;
      }
      &.bottomCenter{
        display: flex;
        align-items:flex-end;
        justify-content:center;
      }
      &.bottomRight{
        display: flex;
        align-items:flex-end;
        justify-content:flex-end;
      }
    }
    &[hideing='hideing']{
      animation:idm_dialog_wrap_shade_animation_hide 0.52s;
      /*Safari and Chrome*/
      -webkit-animation:idm_dialog_wrap_shade_animation_hide 0.52s;
    }
}
@keyframes idm_dialog_wrap_shade_animation
{
from {opacity: 0;}
to {opacity: 1;}
}

@-webkit-keyframes idm_dialog_wrap_shade_animation /*Safari and Chrome*/
{
from {opacity: 0;}
to {opacity: 1;}
}
@keyframes idm_dialog_wrap_shade_animation_hide
{
from {opacity: 1;}
to {opacity: 0;}
}

@-webkit-keyframes idm_dialog_wrap_shade_animation_hide /*Safari and Chrome*/
{
from {opacity: 1;}
to {opacity: 0;}
}
@import "../style/core/layer.scss";



.slide-fade-enter, .slide-fade-leave-to
{
  left:0;top: 0;right: 0;
  position: absolute;
  transform:translateX(100%);
}
.slide-fade-enter-active {
  background-color: white;
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.4s ease;
  background-color: white;
  transform:translateX(100%);
  z-index: 100;
}


.idm_render_engine_page_container+.idm_render_engine_page_container{
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  background-color: white;
}
